.App {
  text-align: center;
  font-family: 'Lato', sans-serif;
}

.pw-logo {
  height: 40vmin;
  pointer-events: none;
  margin-bottom: 3rem;
}

.ig-logo {
  height: 2rem;
}

.centered-text {
  text-align: center;
}

.company-name {
  color: #1a1a1a;
  margin-top: 0;
  padding: 0;
}

.company-description {
  text-align: center;
  padding: 1rem;
  margin-top: 1rem;
  font-size: 2.0rem;
  font-weight: 300;
}

.footer-text {
  text-align: center;
  font-size: 1rem;
  margin: 1.5rem 0rem 1.5rem 0rem;
  font-weight: 150;
}

.App-header {
  background-color: #FFF;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
